import React, {Component} from 'react'
import { Redirect,Link } from 'react-router-dom';
import jwt_decode from 'jwt-decode'
import { listClients,apiGetListPin,apiGetGroupPin,apiListFacturacion } from '../../utils/UserFunctions'
import { apiListPlan } from '../../utils/RadiusFuncions'


import TableComponent from '../Common/TableComponent';

import { textFilter,selectFilter } from 'react-bootstrap-table2-filter';


// import { Container, Button, Row, Col, Spinner } from "reactstrap";
// import { connect } from "react-redux";

class Profile extends Component {
  constructor() {
    super()
    this.state = {
      redirect:false,
      errors: {},
      user:[],
      users_pins:[],
      group_pins:[{factura_ID:"",estado:"",plan:"",cantidad:""}],
      dataTable:[{first_name:"",last_name:"",telefono:""}],
      facturaTable:[{note:"",FechaCreaci√≥n:"",Vendedor:"",Cliente:"",plan:"",cantidad:""}],
      planes:[{1:"",2:""}],
    }
    // this.addPines.bind(this)
  }

  componentDidMount() {
    // const token = localStorage.usertoken
    if(localStorage.usertoken){
      const decoded = jwt_decode(localStorage.usertoken)
      this.setState({
        // id: decoded.id,
        user:decoded
      })
      if(decoded.Rol==="Admin"){
        listClients().then(res=>{
          this.setState({dataTable:res})
        }).catch(err=>{
          console.log({err})
        })
        apiListFacturacion().then(res=>{
          this.setState({facturaTable:res})
        }).catch(err=>{
          console.log({err})
        })
        apiListPlan().then(res=>{
          console.log('planes',res)
          let jsonData = {};
          res.map(r=>{
             return jsonData[r.groupname]=r.groupname
          })
          this.setState({
             planes:jsonData
          })
          }).catch(e=>{
              console.log(e)
          })
      }else{
        apiGetListPin(decoded.ID).then(res=>{
          this.setState({users_pins:res})
        }).catch(e=>{
          console.log(e)
        })
        apiGetGroupPin(decoded.ID).then(res=>{
          this.setState({group_pins:res})
        }).catch(e=>{
          console.log(e)
        })
        
      }
    }else{
      this.setState({redirect:true})
    }
  };

  handelClick = (e,i) =>{
    e.preventDefault();
    console.log(i)
  }
  

  render() {
    if (this.state.redirect){return <Redirect to="/"/>;}
    
    const clientColumns = [
      {
        dataField: "first_name",
        text: "Nombre",
        sort: true,
      },
      {
        dataField: "last_name",
        text: "Apellido",
        sort: true,
      },
      {
        dataField: "telefono",
        text: "Telefono",
        sort: true,
      },
      {
        dataField: "email",
        text: "Correo",
        sort: true,
      },
      {
        dataField: "Estado",
        text: "Estado",
        sort: true,
      },
      {
        dataField: "FechaCreacion",
        text: "Fecha Creaci√≥n",
        sort: true,
      },
      {
        dataField: "FechaModificacion",
        text: "Fecha Modificaci√≥n",
        sort: true,
      },
      {
        dataField: "link",
        text: "Acciones",
        formatter: (rowContent, row) => {
          return (
            <div>
              <Link to={{pathname: '/profile/AsignarProducto', user:row}}>
                <img src='/icons8-add-shopping-cart-50.png' alt="A√±adir Pin" height={'30px'} style={{marginRight:'10px'}}></img>
              </Link>
              <label>|</label>
              <Link to={{pathname:'/profile/listarPines', user:row}}>
                <img src='/icons8-invoice-64.png' alt="Estado de cuenta" height={'30px'} style={{marginLeft:'10px'}}></img>
              </Link>
            </div>
          );
        },
      },
    ]

    const FacturaColumns = [
      // {
      //   dataField: "ID",
      //   text: "ID",
      //   sort: true,
      //   headerStyle: () => {
      //     return { width: "5%" };
      //   },
      // },
      {
        dataField: "ID",
        text: "Factura",
        sort: true,
        filter: textFilter({placeholder:'Buscar Factura...'})
      },
      {
        dataField: "FechaCreaci√≥n",
        text: "Fecha",
        sort: true,
        className:"text-nowrap",
        filter: textFilter({placeholder:'Buscar Fecha...'})
      },
      {
        dataField: "Vendedor",
        text: "Vendedor",
        sort: true,
        filter: textFilter({placeholder:'Buscar Vendedor...'})
      },
      {
        dataField: "Cliente",
        text: "Cliente",
        sort: true,
        filter: textFilter({placeholder:'Buscar Cliente...'})
      },
      {
        dataField: "plan",
        text: "Plan",
        sort: true,
        filter: textFilter({placeholder:'Buscar plan...'})
        // formatter: cell => this.state.planes[cell],
        // filter: selectFilter({
        //   options: this.state.planes
        // })
      },
      {
        dataField: "cantidad",
        text: "Cantidad",
        sort: true,
      }
    ]


    return (
      <div className="profile">
        <div className="profile-section">
          <div className="profile-table">
            <div>
              {/* <div className="profile-busqueda">
                <input
                  type="text"
                  placeholder="busqueda..."
                  style={{
                    borderRadius:"0.5em",
                    border:"1px solid #888"
                  }}
                />
              </div> */}
            {this.state.user.Rol==='Admin'?
            <div>
              <div style={{float:'right'}}>
               <Link to={{pathname: '/accounts/register', user:this.state.user}}>
                  <button>A√±adir usuario</button>
                </Link>
              </div>
              <div>
                <TableComponent columns={FacturaColumns} data={this.state.facturaTable}/>
              </div>
              <div>
                <TableComponent columns={clientColumns} data={this.state.dataTable}/>
              </div>
            </div>
            :
            <div className="listarPines-productos">
              <div>
                <div className="profile-busqueda">
                <input
                  type="text"
                  placeholder="busqueda..."
                  style={{
                    borderRadius:"0.5em",
                    border:"1px solid #888"
                  }}
                />
              </div>
                  <table>
                    <thead>
                        <tr><th colSpan={5}>Estado de los pedidos</th></tr>
                        <tr><th>Factura</th><th>Fecha</th><th>Estado</th><th>Plan</th><th>cantidad</th></tr>
                    </thead>
                    <tbody>
                        {
                          this.state.group_pins.map((e,i)=>{
                              return (
                                <tr key={i}>
                                    <td>{e.factura_ID}</td><td>{e.Fecha}</td><td>{e.estado}</td><td>{e.plan}</td><td>{e.cantidad}</td>
                                </tr>
                              )
                          })
                        }
                    </tbody>
                  </table>
              </div>
              <div>
              {/* <div className="profile-busqueda">
                <input
                  type="text"
                  placeholder="busqueda..."
                  style={{
                    borderRadius:"0.5em",
                    border:"1px solid #888"
                  }}
                />
              </div> */}
                  <table>
                    <thead>
                        <tr><th colSpan={5}>Listado de pines</th></tr>
                        <tr><th>Pin</th><th>Plan</th><th>Factura</th><th>Estado</th><th>Fecha</th></tr>
                    </thead>
                    <tbody>
                        {
                          this.state.users_pins.map((e,i)=>{
                              return (
                                <tr key={i}>
                                    <td>{e.username}</td><td>{e.plan}</td><td>{e.factura_ID}</td><td>{e.STATUS}</td><td>{e.FechaCreacion}</td>
                                </tr>
                              )
                          })
                        }
                    </tbody>
                  </table>
              </div>
            </div>
        }
          </div>
        </div>
      </div>
      </div>
    )
  }
}

export default Profile;