import React, {Component} from 'react'
import { Redirect } from 'react-router-dom';
import { apiListPlan, apiAssingPin } from '../../utils/RadiusFuncions'
import jwt_decode from 'jwt-decode'

import Swal from 'sweetalert2';

class GenerarProducto extends Component {
   constructor(props) {
      super()
      this.state = {
         user: props.location.user,
         vendedorID:'',
         redirect:false,
         inputList:[{plan:"",cantidad:1}],
         planes:[]
      };
   }
   componentDidMount() {
      const user = this.state.user
      if(user){
         this.setState({
            userID:user.ID,
            first_name: user.first_name,
            last_name: user.last_name,
            email: user.email,
            telefono: user.telefono,
            Estado: user.Estado,
            Rol: user.Rol,
            FechaCreacion: user.FechaCreacion,
          })
         const token = localStorage.usertoken
         if(token){
            const decoded = jwt_decode(token)
            this.setState({
               vendedorID:decoded.ID
            })
         }
         apiListPlan().then(res=>{
            const planes = res.map(r=>{
               return r.groupname
            })
            this.setState({
               planes:planes,
               inputList:[{plan:planes[0],cantidad:1}]
            })
            // const inputListInit = [{plan:this.state.plan[0],cantidad=1}]
         })
      }else{
         this.setState({redirect:true})
      }
   }

   onResetArray = () => {
      this.setState({inputList:[{plan:this.state.planes[0],cantidad:1}]});
    };

   onChangeValue = (e,index) => {
      const { name, value } = e.target;
      const list = this.state.inputList;
      list[index][name] = value
      this.setState({});
   };
   
   onAddItem = (e) => {
      e.preventDefault();
      if(this.state.inputList.length<6){
         this.setState({
            inputList: [...this.state.inputList, { plan: this.state.planes[0], cantidad: 1 }]
         });
      }else{
         Swal.fire({
            position: 'center',
            icon: 'info',
            title: `Solo puede agregar hasta ${this.state.inputList.length} productos`,
            showConfirmButton: false,
            timer: 1500
          })
      }
    };
    
   handleRemoveClick = (e,index) => {
      e.preventDefault();
      let list = [...this.state.inputList];
      list.splice(index, 1);
      this.setState(
         {inputList:list}
      );
      // this.setState({})
      
   };

   handleSubmit = (e) =>{
      e.preventDefault();
      
      Swal.fire({
         title: `¬øConfirma que quiere generar nuevos productos?`,
         confirmButtonColor: '#3085d6',
         confirmButtonText: 'Confirmo',
         cancelButtonColor: '#d33',
         cancelButtonText: 'Cancelar!',
         showCancelButton: true,
         input: 'text',
         inputAttributes: {
            autocapitalize: 'off',
            placeholder: 'Comentarios...'
         },
         // icon: 'warning',
         showLoaderOnConfirm: true,
         preConfirm: (note) => {
            apiAssingPin(this.state.userID,this.state.inputList,this.state.vendedorID,note)
            .then(resul=>{
               if(resul.msg==='OK'){
                  Swal.fire({
                     position: 'center',
                     icon: 'success',
                     title: 'Ha sido guardado',
                     showConfirmButton: false,
                     timer: 1500
                   })
               }else{
                  Swal.showValidationMessage(
                     `Error : ${resul.msg}`
                     )
                  }
            })
            .catch(error=>{
               Swal.showValidationMessage(
                  `Error : ${error}`
                  )
               })
            },
            allowOutsideClick: () => !Swal.isLoading()
         }).then((result) => {
            if(result.isConfirmed){
               Swal.fire({
                  position: 'center',
                  icon: 'info',
                  title: 'Procesando...',
                  text:'En un momento se dar√° respuesta',
                  showConfirmButton: false,
                  timer: 1500
                })
            }
         })
   }


   render() {
      if (this.state.redirect){
         return <Redirect to="/profile"/>;
      }
      
      return (
         <div className="pinsGenerator">
            <div className="pinsGenerator-title">
               <table>
                  <thead>
                     <tr><th colSpan={6}>Cliente</th></tr>
                     <tr><th>Nombre</th><th>Apellido</th><th>Telefono</th><th>Correo</th><th>Rol</th><th>Estado</th></tr>
                  </thead>
                  <tbody><tr><td>{this.state.first_name}</td><td>{this.state.last_name}</td><td>{this.state.telefono}</td><td>{this.state.email}</td><td>{this.state.Rol}</td><td>{this.state.Estado}</td></tr></tbody>
               </table>
            </div>
            <div className="pinsGenerator-productos">
               <form onSubmit={e=>this.handleSubmit(e)}>
                  <div style={{width:'520px'}} className="pinsGenerator-productos-box">
                     <div>
                        <label className="pinsGenerator-productos-labelPlan">Plan</label>
                        <label className="pinsGenerator-productos-labelCantidad">Cantidad</label>
                     </div>
                     {this.state.inputList.map((x,i)=>{
                        return (
                           <div key={i}>
                              <select 
                                 value={x.plan}
                                 defaultValue={x.plan}
                                 onChange={e => this.onChangeValue(e,i)}
                                 name="plan"
                                 placeholder="Seleccione..."
                              >
                                 {this.state.planes.map((x,i)=> {return <option key={i} value={x}>{x}</option>})}
                              </select>
                              <input  
                                 value={x.cantidad}
                                 type="number" 
                                 max="1000" min="1" 
                                 defaultValue={x.cantidad}
                                 name="cantidad"
                                 onChange={e => this.onChangeValue(e,i)}
                              />
                              {this.state.inputList.length !== 1 && <button className="button-action" onClick={e => this.handleRemoveClick(e,i)}>Borrar</button>}
                           </div>
                        )
                     })}

                  </div>
                  <div style={{width:'200px'}} className="pinsGenerator-productos-box">
                     <div>
                        <button className="button-ctrl" type="button" onClick={e => this.onAddItem(e)}>Agregar Producto</button>
                     </div>
                     <div>
                        <button className="button-ctrl" type="button" onClick={this.onResetArray}>Reset</button>
                        <button className="button-ctrl" type="button" >Cancelar</button>
                     </div>
                     <div>
                        <input className="button-action" type="submit" value="Generar Producto" />
                     </div>
                  </div>
               </form>
            </div>
            {/* <pre>
            {JSON.stringify({"user":{"id":this.state.userID,"vendedorID":this.state.vendedorID},"planes":this.state.inputList},null,2)}
         </pre> */}
         </div>
      )
   }
}

export default GenerarProducto