
import React, { Component } from 'react'
import { BrowserRouter as Router, Route,Switch } from 'react-router-dom'

import Navbar from './components/Common/Navbar'
import Landing from './components/Common/Landing'
import Login from './components/Account/Login'
import Register from './components/Account/Register'
import Profile from './components/Profile/Profile'
import AsignarProducto from './components/Profile/AsignarProducto'
import listarPines from './components/Profile/ListarPines'

class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
          <Navbar />
          <Route exact path="/" component={Landing} />
          <div className="container">
            <Switch>
              <Route exact path="/accounts/register" component={Register} />
              <Route ex-ct path="/accounts/login" component={Login} />
            {/* </Switch>  
            <Switch> */}
              <Route exact path="/profile" component={Profile} />
              <Route exact path="/profile/AsignarProducto" component={AsignarProducto}/>
              <Route exact path="/profile/listarPines" component={listarPines}/>
              {/* <Route path="*" component={() => "404 NOT FOUND"} /> */}
            </Switch>
          </div>
        </div>
      </Router>
    )
  }
}

export default App
