import React, { Component } from 'react'
import { Redirect } from 'react-router-dom';
import { register } from '../../utils/UserFunctions'
// import jwt_decode from 'jwt-decode'

class Register extends Component {
  constructor(props) {
    super()
    this.state = {
      user: props.location.user,
      errors: {},
      redirect:false,
    }
  }
  componentDidMount() {
    if(this.state.user) {
      if(this.state.user.Rol==='Admin'){
        this.setState({
          first_name: "",
          last_name: "",
          email: "",
          telefono: "",
          password:"",
          user:{}
        })
      }else{
        this.setState({redirect:true})
      }
    }else{
      this.setState({redirect:true})
    }
  }

  onChangeValue = (e)=>{
    this.setState({ [e.target.name]: e.target.value })
  }
  
  onRegister = (e)=>{
    e.preventDefault();
    register({
      first_name: this.state.first_name,
      last_name: this.state.last_name,
      telefono: this.state.telefono,
      email: this.state.email,
      password: this.state.password
    }).then(res => {
      this.props.history.push(`/login`)
    }).catch(e=>{
      console.log(e)
    })
  }

  render() {
    if (this.state.redirect){
      return <Redirect to="/profile"/>;
    }
    return (
      <div className="container">
        <div className="login-box">
            <h2>Registro de usuarios</h2>
            <form>
              <div className="user-box">
                <input required
                  type="text"
                  name="first_name"
                  // placeholder="Ingrese su nombre"
                  value={this.state.first_name}
                  onChange={e => this.onChangeValue(e)}
                />
                <label htmlFor="name">Nombre</label>
              </div>
              <div className="user-box">
                <input required
                  type="text"
                  name="last_name"
                  // placeholder="Ingrese su apellido"
                  value={this.state.last_name}
                  onChange={e => this.onChangeValue(e)}
                />
                <label htmlFor="name">Apellido</label>
              </div>
              <div className="user-box">
                <input required
                  type="email"
                  name="email"
                  // placeholder="Ingrese un email v√°lido"
                  value={this.state.email}
                  onChange={e => this.onChangeValue(e)}
                />
                <label htmlFor="email">Email</label>
              </div>
              <div className="user-box">
                <input required
                  type="text"
                  name="telefono"
                  // placeholder="Ingrese su n√∫mero de contacto v√°lido"
                  value={this.state.telefono}
                  onChange={e => this.onChangeValue(e)}
                />
                <label htmlFor="telefono">Telefono</label>
              </div>
              <div className="user-box">
                <input required
                  type="password"
                  name="password"
                  // placeholder="Ingrese su contrase√±a"
                  value={this.state.password}
                  onChange={e => this.onChangeValue(e)}
                />
                <label htmlFor="password">Password</label>
              </div>
              <a href="/#" onClick={e=>this.onRegister(e)}>          
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                Registrar
              </a>
            </form>
        </div>
        {/* <pre>
          {JSON.stringify({state:this.state},null,2)}
        </pre> */}
      </div>
    )
  }
}

export default Register