import axios from 'axios'
import {urlApi} from '../config/app'


export const apiListPlan = () =>{
  let urlEndPoint = 'radius/listPlan'
  return axios.post(`${urlApi}/${urlEndPoint}`)
    .then(res=>{
      return res.data
    })
}

export const apiAssingPin = (userID,planes,vendedorID,vendedorNote) =>{
  let urlEndPoint = 'radius/assingPin'
  return axios.post(`${urlApi}/${urlEndPoint}`,{"user":{"id":userID,vendedorID,"vendedorNote":vendedorNote},"planes":planes})
    .then(res=>{
      return res.data
    })
}