import React, {Component} from 'react'
import { Redirect } from 'react-router-dom';
import { textFilter,selectFilter } from 'react-bootstrap-table2-filter';
import TableComponent from '../Common/TableComponent';

import { apiGetListPin,apiGetGroupPin,apiGetListPinsByParams,apiListStatusFactura } from '../../utils/UserFunctions'
import { apiListPlan } from '../../utils/RadiusFuncions'

class ListarPines extends Component {
   constructor(props) {
      super()
      this.state = {
         user: props.location.user,
         redirect:false,
         users_pins:[{username:"",plan:"",FechaCreacion:""}],
         group_pins:[{factura_ID:"",estado:"",plan:"",cantidad:""}],
         planes:[{id: 1, groupname: ""}],
         statusFactura:[{id:1}]
      };
   }
   componentDidMount() {
      const user = this.state.user
      if(user){
         this.setState({
            userID:user.ID,
            first_name: user.first_name,
            last_name: user.last_name,
            email: user.email,
            telefono: user.telefono,
            Estado: user.Estado,
            Rol: user.Rol,
            FechaCreacion: user.FechaCreacion
          })
         apiGetListPin(user.ID).then(res=>{
            this.setState({users_pins:res})
         }).catch(e=>{
            console.log(e)
         })
         apiGetGroupPin(user.ID).then(res=>{
            this.setState({group_pins:res})
         }).catch(e=>{
            console.log(e)
         })
         apiListPlan().then(res=>{
            let jsonData = {};
            res.map(r=>{
               return jsonData[r.groupname]=r.groupname
            })
            this.setState({
               planes:jsonData
            })
         }).catch(e=>{
            console.log(e)
         })
         apiListStatusFactura().then(res=>{
            let jsonData = {};
            res.map(r=>{
               return jsonData[r.STATUS]=r.STATUS
            })
            this.setState({
               statusFactura:jsonData
            })
         }).catch(e=>{
            console.log(e)
         })
            
         // })
      }else{
         this.setState({redirect:true})
      }
   }

   listarCuenta = (ev,el)=>{
      ev.preventDefault();
      // console.log(el)
      apiGetListPinsByParams(el).then(res=>{
         this.setState({users_pins:res})
      }).catch(e=>{
         console.log(e)
      })
   }

   render() {
      if (this.state.redirect){
         return <Redirect to="/profile"/>;
      }
      const clientColumns = [
         // {
         //   dataField: "ID",
         //   text: "ID",
         //   sort: true,
         //   headerStyle: () => {
         //     return { width: "5%" };
         //   },
         // },
         {
           dataField: "factura_ID",
           text: "Factura",
           sort: true,
           filter: textFilter({placeholder:'Buscar Factura...'})
         },
         {
           dataField: "username",
           text: "Pin",
           sort: true,
           filter: textFilter({placeholder:'Buscar Pin...'})
         },
         {
           dataField: "plan",
           text: "Plan",
           sort: true,
           formatter: cell => this.state.planes[cell],
           filter: selectFilter({
             options: this.state.planes
           })
         },
         {
           dataField: "STATUS",
           text: "Estado",
           sort: true,
           formatter: cell => this.state.statusFactura[cell],
           filter: selectFilter({
             options: this.state.statusFactura
           })
         },
         {
           dataField: "FechaCreacion",
           text: "Fecha Creaci√≥n",
           sort: true,
         }
      ];
      if(Array.isArray(this.state.users_pins)){
         return (
            <div className="listarPines">
               <div className="listarPines-title">
                  <table>
                     <thead>
                        <tr><th colSpan={6}>Cliente</th></tr>
                        <tr><th>Nombre</th><th>Apellido</th><th>Telefono</th><th>Correo</th><th>Rol</th><th>Estado</th></tr>
                     </thead>
                     <tbody><tr><td>{this.state.first_name}</td><td>{this.state.last_name}</td><td>{this.state.telefono}</td><td>{this.state.email}</td><td>{this.state.Rol}</td><td>{this.state.Estado}</td></tr></tbody>
                  </table>
               </div>
               <div className="listarPines-productos">
                  <div>
                     <div>
                        <TableComponent columns={clientColumns} data={this.state.users_pins}/>
                     </div>
                     {/* <div className="profile-busqueda">
                        <input
                           type="text"
                           placeholder="busqueda..."
                           style={{
                           borderRadius:"0.5em",
                           border:"1px solid #888"
                           }}
                        />
                     </div> */}
                     {/* <table>
                        <thead>
                           <tr><th colSpan={5}>Listado de pines</th></tr>
                           <tr>
                              <th>Factura</th>
                              <th>Pin</th>
                              <th>Plan</th>
                              <th>Estado</th>
                              <th>Fecha</th>
                           </tr>
                        </thead>
                        <tbody>
                           {
                              this.state.users_pins.map((e,i)=>{
                                 return (
                                    <tr key={i}>
                                       <td>{('0000'+e.factura_ID).slice(-4)}</td>
                                       <td>{e.username}</td>
                                       <td>{e.plan}</td>
                                       <td>{e.STATUS}</td>
                                       <td>{e.FechaCreacion}</td>
                                    </tr>
                                 )
                              })
                           }
                        </tbody>
                     </table> */}
                  </div>
                  <div>
                     {/* <div className="profile-busqueda">
                        <input
                           type="text"
                           placeholder="busqueda..."
                           style={{
                           borderRadius:"0.5em",
                           border:"1px solid #888"
                           }}
                        />
                     </div> */}
                     {/* <table>
                        <thead>
                           <tr><th colSpan={5}>Estado de cuenta</th></tr>
                           <tr>
                              <th>Factura</th>
                              <th>Fecha</th>
                              <th>Plan</th>
                              <th>Estado</th>
                              <th>cantidad</th>
                           </tr>
                        </thead>
                        <tbody>
                           {
                              this.state.group_pins.map((e,i)=>{
                                 return (
                                    <tr key={i} onClick={ev => this.listarCuenta(ev,e)} >
                                       <td>{('0000'+e.factura_ID).slice(-4)}</td>
                                       <td>{e.Fecha}</td>
                                       <td>{e.plan}</td>
                                       <td>{e.estado}</td>
                                       <td>{e.cantidad}</td>
                                    </tr>
                                 )
                              })
                           }
                        </tbody>
                     </table> */}
                  </div>
               </div>
            </div>
         )
      }else{
           return ( <div className="listarPines">
                  <div className="listarPines-title">
                     <table>
                        <thead><tr><th>Nombre</th><th>Apellido</th><th>Telefono</th><th>Correo</th><th>Rol</th><th>Estado</th></tr></thead>
                        <tbody><tr>
                           <td>{this.state.first_name}</td>
                           <td>{this.state.last_name}</td>
                           <td>{this.state.telefono}</td>
                           <td>{this.state.email}</td>
                           <td>{this.state.Rol}</td>
                           <td>{this.state.Estado}</td>
                        </tr></tbody>
                     </table>
                  </div>
                  <div className="listarPines-productos">
                  <table>
                     <thead><tr><th>Pin</th><th>Plan</th><th>Fecha Creaci√≥n</th></tr></thead>
                     <tbody><tr><td colSpan="3">Cargando...</td></tr></tbody>
                  </table>
                  </div>
            </div>
           )
         }
      }
}

export default ListarPines