import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom'

import jwt_decode from 'jwt-decode'

class Landing extends Component {
  constructor(...props) {
    super()
    this.state = {
       user: {Rol:'', Estado:'', email:'', telefono:'', FechaCreacion:''}
    };
 }
 componentDidMount() {
    const token = localStorage.usertoken
    if(token){
      const decoded = jwt_decode(token)
      this.setState({
        user:decoded
      })
    }
  }
  
  logOut(e) {
    e.preventDefault()
    localStorage.removeItem('usertoken')
    this.props.history.push(`/`)
  }

  render() {
    const loginRegLink = (
      <div>
        {/* <li>
          <Link to="/accounts/login" className="nav-link">
            Login
          </Link>
        </li> */}
        {/* <li>
          <Link to="/registrar" className="nav-link">
            Suscribirse
          </Link>
        </li> */}
      </div>
    )

    const userLink = (
      <div>
        <li>
          <Link to="/profile" className="nav-link">
            Cuenta
          </Link>
        </li>
        <li>
          <a href="/#" onClick={this.logOut.bind(this)} className="nav-link">
            Salir
          </a>
        </li>
      </div>
    )

    const navbarUser = (
      <div className="navbar-section">
        <div></div>
        <div></div>
        <div>
          <table style={{float:'right'}}>
            <thead>
              <tr><th>Email</th><th>Rol</th><th>Estado</th></tr>
            </thead>
            <tbody>
              <tr><td>{this.state.user.email}</td><td>{this.state.user.Rol}</td><td>{this.state.user.Estado}</td></tr>
            </tbody>
          </table>
        </div>
      </div>
    )

    const navbarUser_non = (
      <div className="profile-section">
        <div className="profile-content">
        </div>  
      </div>
    )

    return (
      <nav className="navbar">
        <div>
          <img src={"/LOGO_TICENERGI3.png"} alt={"logo"} height={'100px'}></img>
        </div>
        <div>
          <div style={{textAlign:'right'}}>
            <ul>
              <div>
                <li className="nav-item">
                  {/* <Link to="/" className="nav-link">
                    Inicio
                  </Link> */}
                </li>
              </div>
              {localStorage.usertoken ? userLink : loginRegLink}
            </ul>
          </div>
          {localStorage.usertoken ? navbarUser : navbarUser_non}
        </div>
      </nav>
    )
  }
}

export default withRouter(Landing)
  